@use 'base/mixins' as m;
@use 'base/variables' as v;

.eventos-registro {
    
    @include m.contenedor;

    @include m.tablet {
        display: flex;
        align-items: flex-start;
        gap: 2rem;
    }
    

    &__listado {
        @include m.tablet {
            flex: 0 0 60%;
        }
    }

    &__grid {
        @include m.grid(1, 3rem);
        @include m.tablet {
            @include m.grid(2, 3rem);
        }
    }

    &__heading {


        &--conferencias,
        &--workshops {
            white-space: nowrap;
            @include m.texto-fluido (2rem, 5rem);
        }

        &--conferencias {
            color: v.$azul;
        }
        &--workshops {
            color: v.$secundario;
            margin-top: 5rem;
        }
    }
    &__fecha {
        margin: 6rem 0 3rem 0;
    }
}

.registro {

    background-color: v.$gris-claro;
    border-radius: 1rem;

    @include m.tablet {
        flex: 0 0 40%;
        position: -webkit-sticky;
        position: sticky;
        margin: 3rem;
        top: 5rem;
    }

    &__heading {
        margin: 1rem 0 3rem 0;
    }
    &__evento {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 2rem;
        &:nth-child(even) {
            background-color: darken(v.$gris-claro, 3);
        }
    }

    &__nombre {
        font-size: 2rem;
        font-weight: v.$bold;
        margin: 0;
    }
    &__eliminar {
       
        all: unset;
        color: v.$rojo;
        &:hover{
            cursor: pointer;
        }
        
    }
}